import React, { useState, useEffect } from 'react';
import { QRCodeCanvas } from 'qrcode.react';
import { toPng } from 'html-to-image';
import { saveAs } from 'file-saver';
import { Input, Button, List, Card, Typography, message } from 'antd';

const { Title } = Typography;

function App() {
  const [name, setName] = useState('');
  const [participants, setParticipants] = useState([]);

  //    localStorage
  useEffect(() => {
    const saved = localStorage.getItem('participants');
    if (saved) {
      setParticipants(JSON.parse(saved));
    }
  }, []);

  //    localStorage
  useEffect(() => {
    localStorage.setItem('participants', JSON.stringify(participants));
  }, [participants]);

  const handleGenerate = () => {
    if (!name.trim()) {
      message.warning('     ');
      return;
    }
    const entry = { name, id: Date.now() };
    setParticipants([...participants, entry]);
    setName('');
    message.success('QR Code    ');
  };

  const handleSaveQRCode = async (id) => {
    const node = document.getElementById(`qr-wrapper-${id}`);
    try {
      const dataUrl = await toPng(node);
      saveAs(dataUrl, `QRCode-${id}.png`);
      message.success('  ');
    } catch (error) {
      console.error('   QR:', error);
      message.error('   ');
    }
  };

  return (
    <div
      style={{
        padding: 30,
        maxWidth: 600,
        margin: '0 auto',
        backgroundColor: '#0b1a33',
        color: 'white',
        minHeight: '100vh',
        direction: 'rtl',
      }}
    >
      <Title level={3} style={{ color: '#aad3ff' }}>
           
      </Title>

      <Input.Group compact style={{ marginBottom: 20 }}>
        <Input
          style={{
            width: '70%',
            backgroundColor: '#14284b',
            color: '#fff',
            borderColor: '#334d73',
          }}
          value={name}
          onChange={(e) => setName(e.target.value)}
          placeholder="    "
          placeholderTextColor="#ccc"
        />
        <Button
          type="primary"
          onClick={handleGenerate}
          style={{ backgroundColor: '#1f3b67', borderColor: '#1f3b67' }}
        >
           QR
        </Button>
      </Input.Group>

      <List
        dataSource={participants}
        renderItem={(p) => {
          const qrValue =
            `: ${p.name}\n` +
            `:   \n` +
            `:  \n` +
            `:  \n` +
            `: ${p.id}`;

          return (
            <List.Item key={p.id}>
              <Card
                title={`: ${p.name}`}
                style={{
                  width: '100%',
                  backgroundColor: '#14284b',
                  color: 'white',
                  borderColor: '#334d73',
                }}
                headStyle={{ color: '#aad3ff' }}
                bodyStyle={{ color: 'white' }}
                actions={[
                  <Button
                    key="save"
                    onClick={() => handleSaveQRCode(p.id)}
                    style={{ backgroundColor: '#1f3b67', color: '#fff' }}
                  >
                     QR
                  </Button>,
                ]}
              >
                <div
                  id={`qr-wrapper-${p.id}`}
                  style={{
                    backgroundColor: '#0b1a33',
                    padding: 20,
                    borderRadius: 10,
                    display: 'inline-block',
                    textAlign: 'center',
                    color: '#ffffff',
                    fontSize: 14,
                    fontFamily: 'Tahoma, sans-serif',
                  }}
                >
                  <QRCodeCanvas value={qrValue} size={150} />
                  <div style={{ marginTop: 10, whiteSpace: 'pre-line' }}>
                    <div>   </div>
                    <div>  </div>
                    <div>  </div>
                  </div>
                </div>
              </Card>
            </List.Item>
          );
        }}
        locale={{ emptyText: '   ' }}
      />
    </div>
  );
}

export default App;
